.services-container {
  width: 100%;
  padding: 75px 100px;
  border-radius: 50px 50px 0px 0px;
  border-top: 1.5px solid rgba(10, 97, 26, 0.3);
  background: rgba(255, 255, 255, 0.75);
  background-image: url('../images/bg-laps-serv.webp');
  background-position: center;
  // background-repeat: no-repeat;
  // background-size: contain;
  @media screen and (max-width: 1919px) {
    padding: 75px 25px;
  }
  @media screen and (max-width: 767px) {
    padding: 35px 15px;
    border-radius: 30px 30px 0px 0px;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
}
.services-title {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: start;
  justify-content: space-between;
  @media screen and (max-width: 767px) {
    flex-direction: column;
    gap: 20px;
  }
}
.services-name {
  color: #04a427;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 110%; /* 22px */
  letter-spacing: -0.2px;
  @media screen and (max-width: 767px) {
    font-size: 15px;
  }
}
.services-info {
  display: flex;
  flex-direction: column;
  gap: 30px;
  width: 1170px;
  @media screen and (max-width: 1919px) {
    width: 907px;
  }
  @media screen and (max-width: 767px) {
    width: 100%;
    gap: 15px;
  }
}
.services-desc {
  color: #06631b;
  font-family: Montserrat;
  font-size: 43px;
  font-style: normal;
  font-weight: 500;
  line-height: 123%; /* 52.89px */
  letter-spacing: 0.86px;
  text-transform: uppercase;
  @media screen and (max-width: 1919px) {
    font-size: 40px;
    letter-spacing: 0.8px;
  }
  @media screen and (max-width: 767px) {
    font-size: 19px;
    letter-spacing: 0.36px;
  }
}
.services-desc-color {
  color: #04a427;
  font-family: 'Cormorant Unicase';
  font-size: 49px;
  font-style: normal;
  font-weight: 500;
  line-height: 123%;
  letter-spacing: 0.98px;
  text-transform: uppercase;
  @media screen and (max-width: 1919px) {
    font-size: 45px;
    letter-spacing: 0.9px;
  }
  @media screen and (max-width: 767px) {
    font-size: 22px;
    letter-spacing: 0.4px;
  }
}
.services-team {
  display: flex;
  flex-direction: row;
  gap: 60px;
  align-items: center;
  justify-content: space-between;
  @media screen and (max-width: 1919px) {
    gap: 50px;
  }
  @media screen and (max-width: 767px) {
    gap: 15px;
    flex-direction: column;
  }
}
.services-team-info {
  display: flex;
  flex-direction: column;
  gap: 20px;
  @media screen and (max-width: 1919px) {
    width: 410px;
  }
  @media screen and (max-width: 767px) {
    gap: 15px;
  }
}
.services-team-text {
  color: #06631b;
  text-align: justify;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%; /* 21.6px */
  letter-spacing: -0.18px;
  @media screen and (max-width: 1919px) {
  }
  @media screen and (max-width: 767px) {
    font-size: 16px;
    letter-spacing: -0.16px;
    text-align: left;
  }
}
.services-team-img {
  border-radius: 40px;
  width: 490px;
  height: 202px;
  @media screen and (max-width: 1919px) {
    display: none;
  }
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.services-team-img-mob {
  display: none;
  @media screen and (max-width: 1919px) {
    display: block;
    border-radius: 20px;
    width: 440px;
    height: 236px;
  }
  @media screen and (max-width: 767px) {
    display: block;
    border-radius: 17px;
    width: 100%;
    // min-height: 180px;
    height: auto;
  }
}

.services-main {
  margin-top: 57px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  @media screen and (max-width: 1919px) {
    margin-top: 51px;
    gap: 24px;
  }
  @media screen and (max-width: 767px) {
    gap: 15px;
    margin: 0;
  }
}
.services-main-line1,
.services-main-line2,
.services-main-block3-line1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 370px;
  width: 100%;
  gap: 45px;
  @media screen and (max-width: 1919px) {
    height: 300px;
    gap: 36px;
  }
  @media screen and (max-width: 767px) {
    height: auto;
    flex-direction: column;
    gap: 15px;
  }
}
.services-main-line2 {
  @media screen and (max-width: 767px) {
    flex-direction: column-reverse;
  }
}
.services-main-block3-line1 {
  @media screen and (max-width: 767px) {
    grid-column: 1 / 3;
    grid-row: 1 / 3;
    // flex-direction: column;
    // width: 100%;
  }
}

.big-box {
  height: 100%;
  width: 761px;
  background-size: cover;
  background-position: center;
  border-radius: 40px;
  padding: 45px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @media screen and (max-width: 1919px) {
    width: 615px;
    padding: 36px;
    border-radius: 24px;
  }
  @media screen and (max-width: 767px) {
    width: 100%;
    min-height: 250px;
    height: auto;
    padding: 30px;
    border-radius: 20px;
  }
}
.sml1-box1 {
  background-image: url('../images/services2.webp');
}
.services-main-box-title {
  color: #06631b;
  font-family: 'Cormorant Unicase';
  font-size: 43px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%; /* 43px */
  letter-spacing: -0.86px;
  text-transform: uppercase;
  @media screen and (max-width: 1919px) {
    font-size: 40px;
    letter-spacing: -0.8px;
  }
  @media screen and (max-width: 767px) {
    font-size: 31px;
    letter-spacing: -0.62px;
    text-align: left;
  }
}
.services-main-box-info {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 172px;
  height: 44px;
  border-radius: 100px;
  background: rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(3.5px);
  color: #06631b;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: -0.4px;
  text-transform: uppercase;
  @media screen and (max-width: 1919px) {
    font-size: 18px;
    border-radius: 80px;
    width: 148px;
    height: 36px;
  }
  @media screen and (max-width: 767px) {
    width: 128px;
    height: 30px;
    // padding: 10px 18px;
    border-radius: 67px;
    font-size: 16px;
    letter-spacing: -0.32px;
  }
}
.small-box {
  height: 100%;
  width: 379px;
  background-size: cover;
  background-position: center;
  border-radius: 40px;
  padding: 45px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @media screen and (max-width: 1919px) {
    width: 306px;
    border-radius: 24px;
    padding: 36px;
  }
  @media screen and (max-width: 767px) {
    width: 100%;
    min-height: 250px;
    height: auto;
    padding: 30px;
    border-radius: 20px;
  }
}
.sml1-box2 {
  background-image: url('../images/services3.webp');
}
.mid-box {
  width: 489px;
  height: 370px;
  border-radius: 40px;
  border-top: 1.5px solid rgba(4, 164, 39, 0.4);
  border-bottom: 1.5px solid rgba(4, 164, 39, 0.4);
  background: #f3fff6;
  backdrop-filter: blur(3.5px);
  padding: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (max-width: 1919px) {
    width: 395px;
    height: 300px;
    border-radius: 32px;
    padding: 28px;
  }
  @media screen and (max-width: 767px) {
    width: 100%;
    height: auto;
    border-radius: 27px;
    padding: 30px 5px;
  }
  span {
    color: #04a427;
    text-align: center;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%; /* 26px */
    text-transform: uppercase;
    @media screen and (max-width: 1919px) {
      font-size: 18px;
    }
    @media screen and (max-width: 767px) {
      font-size: 15px;
    }
  }
}
.mid-box.mid-box-mob {
  @media screen and (max-width: 767px) {
    // width: 290px;
    width: 100%;
    height: 136px;
    border-radius: 27px;
    grid-column: 1 / 3;
    grid-row: 5 / 6;
  }
}
.sml1-box4 {
  background-image: url('../images/services4.webp');
}
.sml1-box5 {
  background-image: url('../images/services5.webp');
}

.services-main-block3 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  @media screen and (max-width: 1919px) {
    gap: 27px;
  }
  @media screen and (max-width: 767px) {
    display: grid;
    width: 100%;
    gap: 15px 9px;
    grid-template-columns: auto;
    grid-template-rows: repeat(7, auto);
    justify-content: center;
  }
}
.services-main-block3.desctop {
  display: flex;
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.mob-block3 {
  display: none;
  @media screen and (max-width: 767px) {
    display: grid;
  }
}

.services-main-block3-left {
  display: flex;
  flex-direction: column;
  gap: 30px;
  @media screen and (max-width: 767px) {
    gap: 15px;
    // border: 2px solid red;
  }
}
.sml1-box6 {
  background-image: url('../images/services6.webp');
  @media screen and (max-width: 767px) {
    width: 100%;
    // width: 290px;
    background-image: url('../images/services6-mob.webp');
    // background-size: cover;
    grid-column: 1 / span 2;
    grid-row: 1;
  }
}
.sml1-box7 {
  background-image: url('../images/services7.webp');
  @media screen and (max-width: 767px) {
    // width: 290px;
    width: 100%;
    grid-column: 1 / span 2;
    grid-row: 2;
  }
}
.services-main-block3-right {
  display: flex;
  flex-direction: column;
  gap: 30px;
  @media screen and (max-width: 767px) {
    // display: grid;
    // display: block;
  }
}
.services-main-block3-line2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  @media screen and (max-width: 767px) {
    // flex-direction: column;
  }
}

.sml1-box8 {
  width: 489px;
  height: 528px;
  border-radius: 40px;
  background-image: url('../images/services8.webp');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  @media screen and (max-width: 1919px) {
    width: 395px;
    height: 426px;
    border-radius: 24px;
  }
  @media screen and (max-width: 767px) {
    background-image: url('../images/services8-mob.webp');
    // width: 290px;
    width: 100%;
    border-radius: 20px;
    min-height: 250px;
    height: auto;
    grid-column: 1 / span 2;
    grid-row: 3;
  }
}
.services-main-block3-line2-left {
  display: flex;
  flex-direction: column;
  gap: 25px;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: 1919px) {
    gap: 16px;
    // align-items: center;
  }
  @media screen and (max-width: 767px) {
    // width: 145px;
    gap: 13px;
    grid-column: 1 / 2;
    grid-row: 4 / 5;
    // align-self: center;
    justify-self: center;
  }
  span {
    width: 134px;
    color: #06631b;
    font-family: 'Cormorant Unicase';
    font-size: 23px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%; /* 23px */
    letter-spacing: -0.46px;
    text-transform: uppercase;
    @media screen and (max-width: 1919px) {
      font-size: 21px;
      letter-spacing: -0.42px;
      text-align: center;
    }
  }
}
.sml1-box9 {
  width: 215px;
  height: 215px;
  border-radius: 40px;
  background-image: url('../images/services9.webp');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  @media screen and (max-width: 1919px) {
    width: 174px;
    height: 174px;
    border-radius: 24px;
  }
  @media screen and (max-width: 767px) {
    background-image: url('../images/services9-mob.webp');
    border-radius: 20px;
    width: 136px;
    height: 136px;
  }
}
.services-main-block3-line2-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 30px;
  @media screen and (max-width: 767px) {
    gap: 15px;
    align-self: center;
    justify-self: center;
  }
}
.services-main-block3-line2-right > div:first-child {
  @media screen and (max-width: 767px) {
    gap: 15px;
    width: 136px;
    grid-column: 2 / 3;
    grid-row: 4 / 5;
    // border: 2px solid red;
  }
}

.smbl2ru-mob {
  display: none;
  @media screen and (max-width: 767px) {
    display: block;
    width: 134px;
    color: #06631b;
    font-family: 'Cormorant Unicase';
    font-size: 23px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%; /* 23px */
    letter-spacing: -0.46px;
    text-transform: uppercase;
  }
}
.services-main-block3-line2-right-up {
  position: relative;
  width: 627px;
  height: 694px;
  border-radius: 40px;
  background-image: url('../images/services10.webp');
  overflow: hidden;
  background-position: center;
  background-size: cover;
  @media screen and (max-width: 1919px) {
    width: 507px;
    height: 561px;
    border-radius: 32px;
  }
  @media screen and (max-width: 767px) {
    border-radius: 20px;
    width: 136px;
    height: 136px;
  }
  div {
    position: absolute;
    top: 0;
    width: 627px;
    height: 120px;
    border-radius: 30px 30px 0px 0px;
    background: rgba(243, 255, 246, 0.7);
    backdrop-filter: blur(3px);
    display: flex;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 1919px) {
      width: 507px;
      height: 97px;
      backdrop-filter: blur(2.4px);
    }
    @media screen and (max-width: 767px) {
      display: none;
    }
    span {
      color: #06631b;
      text-align: center;
      font-family: 'Cormorant Unicase';
      font-size: 43px;
      font-style: normal;
      font-weight: 500;
      line-height: 100%; /* 43px */
      letter-spacing: -0.86px;
      text-transform: uppercase;
      @media screen and (max-width: 1919px) {
        font-size: 40px;
      }
    }
  }
}

.services-main-block3-line2-right-down {
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 767px) {
    // display: grid;
    // display: block;
    grid-column: 1 / 3;
    grid-row: 6 / 7;
  }
}
.snake-mob {
  @media screen and (max-width: 767px) {
    // grid-column: 1 / 2;
    // grid-row: 6 / 7;
    .services-main-box-title {
      font-size: 20px;
    }
    // align-self: center;
    justify-self: center;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
}
