  @media screen and (max-width: 767px) {
    width: 136px;
    height: 136px;
    padding: 0;
    border-radius: 20px;
    // grid-column: 1 / 2;
    // grid-row: 6 / 7;
  }
}
.box12 {
  display: flex;
  flex-direction: column;
  gap: 25px;
  align-items: center;
  @media screen and (max-width: 1919px) {
    gap: 13px;
  }
  @media screen and (max-width: 767px) {
    width: 145px;
    grid-column: 2 / 3;
    grid-row: 6 / 7;
    align-self: center;
    justify-self: center;
  }
  span {
    width: 227px;
    color: #06631b;
    text-align: center;
    font-family: 'Cormorant Unicase';
    font-size: 23px;
    font-style: normal;
    font-weight: 500;
    line-height: 110%; /* 25.3px */
    letter-spacing: -0.46px;
