    height: 145px;
    border-radius: 20px;
  }
}
.sml3-box13 {
  position: relative;
  margin-top: auto;
  width: 489px;
  height: 317px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 40px;
  border-top: 1.5px solid #04a427;
  border-bottom: 1.5px solid #04a427;
  backdrop-filter: blur(3.5px);
